<template>
  <div class="cpt-expression" @click="fetchMarkUrk">
    <img v-show="src" :src="src">
    <loading v-show="!src"></loading>
  </div>
</template>

<script>
import crun from '@/common/crun'
import copy from '../util/copy'
import Loading from './loading'
import showLinks from './showLinks'

export default {
  props: {
    exp: Object
  },

  data () {
    return {
      src: ''
    }
  },

  watch: {
    exp: 'fetchImgData'
  },

  created () {
    this.fetchImgData()
  },

  methods: {
    fetchImgData () {
      const { link } = this.exp
      if (!link) return
      crun.$emit('url-to-base64', link).then(base64 => {
        this.src = base64
      })
    },

    fetchMarkUrk () {
      if (!this.src) return
      this.$swal('......', { button: false })
      crun.$emit('uniform-url', this.src).then(({ url, err = '!', server }) => {
        if (!url) {
          return this.picBedErrHandler(server, err)
        }
        this.$swal({
          content: showLinks(url),
          buttons: false
        })

        // const mdUrl = `![](${url})`
        // copy(mdUrl, ok => {
        //   if (!ok) return
        //   this.$swal({
        //     title: '',
        //     text: mdUrl,
        //     icon: 'success',
        //     buttons: false,
        //     timer: 2000
        //   })
        // })
      })
    },

    picBedErrHandler (server, err) {
      if (server === 'weibo') {
        this.$swal({
          text: `${err}`,
          icon: 'warning',
          buttons: true
          }).then(v => {
            if (!v) return
             window.open('http://weibo.com/?topnav=1&mod=logo')
          })
        } else {
          this.$swal({
          text: `emmm... `,
          icon: 'warning',
          buttons: true
        })
      }
    }
  },

  components: {
    Loading
  }
}
</script>

<style lang="scss">
.cpt-expression {
  position: relative;
  z-index: 100;
  width: 200px;
  height: 200px;
  padding: 10px;
  margin: 20px 0;

  cursor: pointer;

  img {
    display: block;
    width: 100%;
    height: 100%;
  }
}
</style>



